// tslint:disable: max-line-length
import { Injectable } from '@angular/core';
import { Observable, of } from 'rxjs';

import { Entity, Repository } from '@farris/devkit';

import { ChangeDetail, RequestInfo } from '../types';

import { BefRestService } from '../bef_rest_service';
import { BefChangeBuilder } from '../bef_change_builder';
import { BefChangeHandler } from '../bef_change_handler';

import { BefEntityManager } from '../bef_entity_manager';
import { BefVariableManager } from '../bef_variable_manager';
import { BefDataService } from '../bef_data_service';

/**
 * 适配老的API，保持兼容
 */
@Injectable()
abstract class DeprecatedBefRepository<T extends Entity> extends Repository<T> {

  /**
   * 实体管理器
   * @internal
   */
  public entityManager: BefEntityManager<T>;

  /**
   * 变量管理器
   * @internal
   */
  public variableManager: BefVariableManager;

  /**
   * 取数服务
   * @internal
   */
  protected dataService: BefDataService<T>;


  // #region 老的属性包装成get方法进行兼容

  /**
   * 取数服务
   */
  public get restService(): BefRestService {
    return this.dataService.restService;
  }

  /**
   * 实体变更（前端）=> BE变更（后端）
   */
  public get changeBuilder(): BefChangeBuilder {
    return this.entityManager.changeBuilder;
  }

  /**
   * BE变更（后端）=> 实体变更（前端）
   */
  public get changeHandler(): BefChangeHandler {
    return this.entityManager.changeHandler;
  }

  // #endregion


  /**
  * 构造函数
  */
  constructor() {
    super();
  }

  // #region 数据访问

  /**
  * 获取实体集合
  */
  public getList(): Observable<T[]> {
    return this.dataService.getList(null, null, null, null);
  }

  /**
   * 获取单个实体
   * @param id 主实体id
   */
  public getById(id: string): Observable<T> {
    return this.dataService.getById(id);
  }

  /**
   * 根据id更新实体
   * @param id 实体id
   */
  public updateById(id: string): Observable<T> {
    return this.dataService.updateById(id);
  }

  /**
   * 创建新实体，并加载
   */
  public create(defaultValue?: any): Observable<any> {
    return this.dataService.create(defaultValue);
  }

  /**
   * 追加实体
   */
  public append(defaultValue?: any): Observable<T> {
    return this.dataService.append(defaultValue);
  }

  /**
   * 创建子实体，并追加
   * @param fpath 父路径，格式形如：/1/edus（从表）或/1/edus/2/grades（从从表）
   */
  public appendByPath(fpath: string): Observable<any> {
    return this.dataService.appendByPath(fpath);
  }

  /**
   * 根据id删除实体
   * @param id 内码
   */
  public removeById(id: string, ifSave?: boolean): Observable<boolean> {
    return this.dataService.removeById(id, ifSave);
  }

  /**
   * 批量删除
   * @param id 内码
   * @param ifSave 是否保存
   */
  public removeByIds(ids: string[], ifSave?: boolean): Observable<any> {
    return this.dataService.removeByIds(ids, ifSave);
  }

  /**
   * 删除子级
   * @param path 路径
   * @param id   内码
   */
  public removeByPath(fpath: string, id: string): Observable<boolean> {
    return this.dataService.removeByPath(fpath, id);
  }
  /**
   * 将id对应的实体的变更提交的服务器端
   */
  public updateChangesById(id: string): Observable<boolean> {
    return this.dataService.updateChangesById(id);
  }

  /**
   * 提交path对应实体的变更
   */
  public updateChangesByPath(path: string, id: string): Observable<any> {
    throw new Error('Not Implement');
  }
  /**
   * 提交所有变更
   */
  public updateAllChanges(): Observable<boolean> {
    return this.dataService.updateAllChanges();
  }

  /**
   * 应用变更
   */
  public applyChanges(): Observable<boolean> {
    return this.dataService.applyChanges();
  }

  /**
   * 提交数据变更和变量变更，通过retrieve模拟
   * @internal
   */
  public updateDataAndVariableChanges(): Observable<any> {
    const filterConditions: any[] = [];
    const sortConditions: any[] = [];
    const entityFilter = {
      FilterConditions: filterConditions,
      SortConditions: sortConditions,
      IsUsePagination: true,
      Pagination: {
        PageIndex: 1,
        PageSize: 1,
        PageCount: 0,
        TotalCount: 0
      }
    };
    const entityFilterJson = JSON.stringify(entityFilter);
    const requestInfo = this.restService.buildRequestInfo();
    // 没有变更时不再提交变更
    if (this.isEmptyRequestInfo(requestInfo)) {
      return of(null);
    }
    const url = `${this.restService.baseUri}/extension/query?entityFilter=${entityFilterJson}`;
    const body = requestInfo;
    const options = {
      body
    };
    return this.restService.invoke(url, 'PUT', null, options, false, true, true);
  }

  /**
   * 应用id对应数据的变更
   */
  public applyChangesById(id: string): Observable<boolean> {
    return this.dataService.applyChangesById(id);
  }

  /**
   * 取消变更
   */
  public cancelChanges(options?: any): Observable<boolean> {
    return this.dataService.cancelChanges(options);
  }

  // #endregion


  // #region 数据变更集处理

  /**
   * 清空所有本地实体的变更集
   */
  public clearAllEntityChanges(): void {
    return this.entityManager.clearAllEntityChanges();
  }

  /**
   * 获取数据变更
   */
  public getDataChangeDetails(): ChangeDetail[] {
    return this.entityManager.buildAllEntityChangeDetails();
  }

  /**
   * 处理数据变更
   */
  public handleDataChangeDetails(changeDetails: ChangeDetail[]): void {
    return this.entityManager.handleDataChangeDetails(changeDetails);
  }

  // #endregion


  // #region 变量变更集处理

  /**
   * 获取变量变更
   */
  public getVariableChangeDetail(): ChangeDetail {
    return this.variableManager.buildChangeDetail();
  }

  /**
   * 处理变量变更
   */
  public handleVariableChangeDetail(changeDetail: ChangeDetail) {
    this.variableManager.handleChangeDetail(changeDetail);
  }

  // #endregion
  /**
   * 变更集是否为空
   * @param requestInfo requestInfo
   */
  private isEmptyRequestInfo(requestInfo: RequestInfo) {
    if (!requestInfo) {
      return true;
    }
    if ((!requestInfo.dataChange || requestInfo.dataChange.length < 1) && (!requestInfo.variableChange || Object.keys(requestInfo.variableChange).length < 1)) {
      return true;
    }
    return false;
  }
}

export { DeprecatedBefRepository };
