import { Injectable, Injector } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { FrameworkSessionService } from '../framework_session_service';
import {
  BeSessionStorageStrategy, SessionStorageBeSessionStorageStrategy
} from './bef_session_storage_strategy';
import {
  BefSessionHandlingStrategy, BefUnifiedSessionHandlingStrategy, BefSeparatedSessionHandlingStrategy
} from './bef_session_handling_strategy';


/**
 * BeSession处理策略工厂
 */
@Injectable()
class BefSessionHandlingStrategyFactory {

  /**
   * 创建BeSession处理策略
   */
  public create(
    handlingStrategyName: string, frmSessionService: FrameworkSessionService,
    beBaseUri: string, httpClient: HttpClient, injector: Injector
  ): BefSessionHandlingStrategy {

    const storageStrategy = this.createStorageStrategy();
    const beSessionUri = this.createBeSessionUri(beBaseUri);

    if (handlingStrategyName === 'UnifiedSession') {
      return new BefUnifiedSessionHandlingStrategy(storageStrategy, frmSessionService, beSessionUri, injector);
    } else {
      return new BefSeparatedSessionHandlingStrategy(storageStrategy, frmSessionService, httpClient, beSessionUri, injector);
    }
  }

  /**
   * 创建BeSession缓存策略
   */
  private createStorageStrategy(): BeSessionStorageStrategy {
    return new SessionStorageBeSessionStorageStrategy();
  }

  /**
   * 创建BeSession地址
   */
  private createBeSessionUri(beBaseUri: string): string {
    return `${beBaseUri}/service/createsession`;
  }
}

export { BefSessionHandlingStrategyFactory };


