export class Uri {
    constructor(private url: string, private paths: { [propName: string]: any } = null) {
        if (!!this.paths) {
            Object.keys(paths).forEach(path => {
                if (this.url.indexOf(`{${path}}`) !== -1) {
                    this.url = this.url.replace(`{${path}}`, `${paths[path]}`);
                }
            });
        }
    }
    public toString() {
        return this.url;
    }
}
