import { RequestInfo } from './types';
import { BefRepository } from './bef_repository';

/**
 * BefProxy工具类
 * @summary
 * 1、BefProxy辅助工具类；
 * 2、暂时无法归类的代码封装；
 */
class BefProxyUtil {

  /**
   * 构造RequestInfo
   */
  public static buildRequestInfo(befRepository: BefRepository<any>): RequestInfo {
    const requestInfo: RequestInfo = {
      dataChange: befRepository.entityManager.buildAllEntityChangeDetails(),
      variableChange: befRepository.variableManager.buildChangeDetail()
    };
    return requestInfo;
  }
}

export { BefProxyUtil };
