import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { ComboHttpService } from '@farris/ui-combo-list';
import { Repository } from '@farris/devkit';
import { BefRepository } from './bef_repository';
import { HttpHeaders } from '@angular/common/http';

@Injectable()
export class DefaultComboHttpService implements ComboHttpService {
  private befRest: BefRepository<any>;

  constructor(repository: Repository<any>) {
    this.befRest = repository as BefRepository<any>;
  }
  getData(url: string, params = {}, method = 'GET'): Observable<any> {
    const headers = new HttpHeaders({ 'content-type': 'application/json' })
    // return this.befRest.restService.invoke(url, method, params, { headers });
    if (method.toLowerCase() !== 'get') {
      return this.befRest.restService.invoke(url, method, null, { headers, body: params });
    } else {
      return this.befRest.restService.invoke(url, method, params, { headers });
    }
  }
}
