import { HttpHeaders } from '@angular/common/http';

class HttpHeaderUtil {

  /**
   * PlainObject => HttpHeaders
   */
  public static convertToHeaders(headers: any) {
    if (!headers) {
      headers = new HttpHeaders();
    }

    // 已经是HttpHeaders，直接返回
    const isHttpHeaders = headers instanceof HttpHeaders;
    if (isHttpHeaders === true) {
      return headers;
    }
    if (Object.keys(headers).length > 0) {
      let httpHeaders = new HttpHeaders();
      Object.keys(headers).forEach(prop => {
        httpHeaders = httpHeaders.append(prop, headers[prop]);
      });
      headers = httpHeaders;
    } else {
      headers = new HttpHeaders();
    }
    return headers;
  }

  // #region 添加特定含义的header

  /**
   * 追加SessionId头
   * @param headers HttpHeaders对象
   * @param context BeSessionId
   */
  public static appendSessionId(headers: HttpHeaders, sessionId: string): HttpHeaders {
    return headers.append('SessionId', sessionId);
  }

  /**
   * 追加CommonVariable头
   * @param headers HttpHeaders对象
   * @param context 框架会话token，等价于原来的SessionId
   */
  public static appendCafRuntimeCommonVariable(headers: HttpHeaders, commonVariable: string): HttpHeaders {
    return headers.append('X-CAF-Runtime-CommonVariable', commonVariable);
  }

  /**
   * 追加X-CAF-Runtime-Context头
   * @param headers HttpHeaders对象
   * @param context 等价于BeSessionId
   */
  public static appendCafRuntimeContext(headers: HttpHeaders, context: string): HttpHeaders {
    return headers.append('X-CAF-Runtime-Context', context);

  }

  /**
   * 追加Content-Type头
   * @param headers HttpHeaders对象
   * @param context 提交内容的MIME类型，默认为application/json
   */
  public static appendContextType(headers: HttpHeaders, contentType?: string): HttpHeaders {
    contentType = contentType ? contentType : 'application/json';
    return headers.append('Content-Type', contentType);
  }
  public static appendRequireMessage(headers: HttpHeaders, requireMessage = true) {
    return headers.append('Bff-Require-Message', requireMessage.toString());
  }
  /**
   * 组合表单复用，表单实例id
   * @param headers headers
   * @param funcInstId 表单实例id
   */
  public static appendFuncInstId(headers: HttpHeaders, funcInstId: string) {
    return headers.append('Func-Inst-Id', funcInstId);
  }
  /**
   * http headers to json
   * @param headers headers
   */
  public static toJson(headers: HttpHeaders) {
    if (!(headers instanceof HttpHeaders)) {
      return null;
    }
    const result = {};
    headers.keys().forEach(header => {
      const values = headers.getAll(header);
      if (values.length > 1) {
        result[header] = values;
      } else {
        result[header] = values[0] || '';
      }
    });
    return result;
  }
  // #endregion

}

export { HttpHeaderUtil };
