import { Injectable, Optional } from '@angular/core';
import { EMPTY, Observable } from 'rxjs';
import { switchMap, map, tap, catchError } from 'rxjs/operators';
import { ILookupHttpService, RemoteParams, LookupGridResult } from '@farris/ui-lookup';
import { Repository, FrameContext } from '@farris/devkit';
import { BefRepository } from './bef_repository';
import { ResponseInfo } from './types';

/**
 * 帮助Rest取数服务
 */
@Injectable()
export class BefLookupRestService implements ILookupHttpService {

  private befRepository: BefRepository<any>;

  /**
   * 帮助取数上下文
   */
  public context: any;

  /**
   * 构造函数
   */
  constructor(
    repository: Repository<any>,
    @Optional() private frameContext: FrameContext
  ) {
    this.befRepository = <BefRepository<any>>repository;
    this.registerDestroyEvent();
  }
  private registerDestroyEvent() {
    if (this.frameContext && this.frameContext.destorySignal) {
      this.frameContext.destorySignal.subscribe(() => {
        this.frameContext = null;
        this.befRepository = null;
      });
    }
  }

  public getData(helpMetadataId: string, data?: RemoteParams): Observable<LookupGridResult> {
    const tableName = helpMetadataId.split('.')[0];
    const labelId = helpMetadataId.split('.')[1];
    data = data || {};
    if (this.frameContext) {
      const primaryValue = this.frameContext.bindingData.list.currentId;
      data['currentForm'] = {
        id: primaryValue
      };
    }
    const enableExtendLoadMethod = this.ifEnableExtendLoadMethod(helpMetadataId);
    if (enableExtendLoadMethod === true) {
      return this.extendGetHelpData(labelId, tableName, data);
    }
    return this.getHelpData(labelId, tableName, data);
  }
  public saveUserSettings(data) {
    const url = '/api/runtime/bcc/v1.0/datagrid/settings';
    return this.befRepository.restService.invoke(url, 'POST', null, { body: data }, false).pipe(
      catchError(error => {
        const formAppContext = this.befRepository.appContext.getFormAppContext();
        this.befRepository.restService.eventBus.post('Exception', '', 'onException', error, formAppContext);
        return EMPTY;
      })
    );
  }

  public getUserSettings(key) {
    const url = '/api/runtime/bcc/v1.0/datagrid/settings/' + key;
    return this.befRepository.restService.invoke(url, 'GET', null, null, false).pipe(
      catchError(error => {
        const formAppContext = this.befRepository.appContext.getFormAppContext();
        this.befRepository.restService.eventBus.post('Exception', '', 'onException', error, formAppContext);
        return EMPTY;
      })
    );
  }

  /**
   * 是否启用扩展取数方法
   */
  private ifEnableExtendLoadMethod(helpMetadataId: string) {

    // 优先使用context里的设置
    if (this.context && this.context.hasOwnProperty('enableExtendLoadMethod')) {
      return this.context.enableExtendLoadMethod;
    }

    // context没有设置时，继续使用通过指令设置的开关
    let enableExtendLoadMethod = false;
    if (this.frameContext) {
      const befApiUrl = this.frameContext.repository.apiUri;
      const enableKey = `${helpMetadataId}@${befApiUrl}`;
      enableExtendLoadMethod = this.frameContext.getParam(enableKey);
    }
    return enableExtendLoadMethod;
  }

  /**
   * 老的帮助取树
   */
  private getHelpData(labelId: string, tableName: string, data: any): Observable<LookupGridResult> {
    const url = `${this.befRepository.restService.baseUri}/elementhelps/${labelId}`;
    const update$ = this.befRepository.updateDataAndVariableChanges();
    
    const result$ = update$.pipe(
      switchMap(() => {
        // tslint:disable-next-line: max-line-length
        return this.befRepository.restService.invoke(url, 'GET', { nodeCode: tableName, queryParam: JSON.stringify(data) }, null, false).pipe(
          catchError(error => {
            const formAppContext = this.befRepository.appContext.getFormAppContext();
            this.befRepository.restService.eventBus.post('Exception', '', 'onException', error, formAppContext);
            return EMPTY;
          })
        );
      })
    );
    return result$;
  }

  /**
   * 扩展的帮助取数
   */
  private extendGetHelpData(labelId: string, tableName: string, data: any): Observable<LookupGridResult> {
    const url = `${this.befRepository.restService.baseUri}/extension/elementhelps`;
    const body = {
      labelId: labelId,
      nodeCode: tableName,
      queryParam: data,
      requestInfo: this.befRepository.restService.buildRequestInfo()
    };
    const options = {
      body: body
    };

    const result$ = this.befRepository.restService.invoke(url, 'PUT', null, options, false, true, true);
    return result$.pipe(
      map((responseInfo: ResponseInfo) => {
        return responseInfo && responseInfo.returnValue || null;
      }),
      catchError(error => {
        const formAppContext = this.befRepository.appContext.getFormAppContext();
        this.befRepository.restService.eventBus.post('Exception', '', 'onException', error, formAppContext);
        return EMPTY;
      }
      ),
    );
  }

  private convert2TreeDataWithPathCode(data: any[], layer = 1, parentPathCode = '01') {
    let nodes = data.filter(d => d.layer === layer && d.pathcode === parentPathCode);
    if (layer > 1) {
      nodes = data.filter(d => d.layer === layer && d.pathcode.substr(0, (layer - 1) * 2) === parentPathCode);
    }
    if (nodes.length) {
      const treeNodes = nodes.map(n => {
        return {
          data: n,
          children: []
        };
      });

      treeNodes.forEach(tn => {
        const _tns = this.convert2TreeDataWithPathCode(data, tn.data.layer + 1, tn.data.pathcode);
        tn.children.push(..._tns);
      });

      return treeNodes;
    }
  }
}