import { Type } from '@angular/core';
import { Entity, EntityList, EntityCollection } from '@farris/devkit';
import { EntityUtil } from './entity_util';
import { ChangeDetailType, ChangeDetail } from './types';

/**
 * 处理服务器端变更
 */
class BefChangeHandler {

  /**
   * 构造函数
   */
  constructor() {
  }

  /**
   * 处理Bef变更集
   */
  handle(
    entityType: Type<Entity>,
    entityCollection: EntityCollection<Entity>,
    changeDetails: ChangeDetail[]
  ) {
    this.handleChangeDetails(entityType, entityCollection, changeDetails);
  }

  /**
   * 处理Bef变更集（批量）
   * @param entities
   */
  handleChangeDetails(
    entityType: Type<Entity>,
    entityList: EntityList<Entity> | EntityCollection<Entity>,
    changeDetails: ChangeDetail[]
  ) {
    if (!changeDetails) {
      return;
    }

    changeDetails.forEach((changeDetail: ChangeDetail) => {
      const id = (changeDetail.ChangeInfo.dataId || changeDetail.ChangeInfo.DataId) as string;
      const entity = this.getEntityById(entityList, id);
      if (!entity) {
        return;
      }
      this.handleChangeDetail(entityType, entity, changeDetail);
    });
  }

  /**
   * 处理Bef变更集（单条）
   */
  handleChangeDetail(
    entityType: Type<Entity>,
    entity: Entity,
    changeDetail: ChangeDetail
  ) {

    if (!changeDetail || !entity) {
      return;
    }

    // 只处理值变更，其他变更待进一步验证。
    if (changeDetail.ChangeType !== ChangeDetailType.Modify) {
      return;
    }

    const changeInfo = changeDetail.ChangeInfo;
    Object.keys(changeInfo).forEach((propName: string) => {
      const { propType, propEntityType } = EntityUtil.getPropInfo(entityType, propName);
      if (propType === 'NgField') {

        // 简单属性：更新值
        entity[propName] = changeInfo[propName];
      } else if (propType === 'NgObject') {
        const childEntity = entity[propName] as Entity;
        if (childEntity.primaryKey) {

          // 关联对象：重新加载数据
          const childEntityData = changeInfo[propName];
          childEntity.load(childEntityData);

        } else {

          // 值对象：递归处理变更
          const childChangeDetail = changeInfo[propName] as ChangeDetail;
          this.handleChangeDetail(propEntityType, childEntity, childChangeDetail);
        }

      } else if (propType === 'NgList') {

        // 子列表：递归处理变更集合
        const childEntityList = entity[propName] as EntityList<Entity>;
        const childChangeDetails = changeInfo[propName] as ChangeDetail[];
        this.handleChangeDetails(propEntityType, childEntityList, childChangeDetails);
      }
    });
  }

  /**
   * 根据id获取实体，屏蔽EntityCollection和EntityList之间的差异
   */
  private getEntityById(entityList: EntityList<Entity> | EntityCollection<Entity>, id: string): Entity {
    let target: Entity;
    if (entityList instanceof EntityCollection) {
      target = entityList.getEntityById(id);
    } else {
      target = entityList.get(id);
    }
    return target ? target : null;
  }


}

export { BefChangeHandler };
