import { Injectable, Inject } from '@angular/core';
import { BE_SERVER_URI_TOKEN } from './tokens';

@Injectable()
export class UriService {
    constructor(@Inject(BE_SERVER_URI_TOKEN) private serverUri: string) {
    }
    /**
     * 扩展eapi基路径
     * @param apiUri eapi基路径
     */
    public extendUri(apiUri: string): string {
        return `${this.serverUri}/${apiUri}`;
    }
}
