import { Injector } from '@angular/core';
import { DataTypeInfo, Entity, EntityCollection, EntityTypeFactory } from '@farris/devkit';
import { Observable } from 'rxjs';
import { BefEntityManager } from './bef_entity_manager';
import { BefRepository } from './bef_repository';

class PresetBefRepository<T extends Entity> extends BefRepository<T> {
  batchAppend(defaultValues: any[]): Observable<any> {
    throw new Error('Method not implemented.');
  }

  constructor(injector: Injector, public schema: any) {
    super(injector);
    const entityTypeFactory: EntityTypeFactory = new EntityTypeFactory();
    this.entityType = entityTypeFactory.create(schema);
    this.entityTypeInfo = new DataTypeInfo(this.entityType);
    this.entityCollection = new EntityCollection(this.entityType);
    // 初始化实体管理器
    this.entityManager = new BefEntityManager(this.entityCollection);
  }
}

export { PresetBefRepository };
